.about {
  padding-top: 125px;
  background-color: $color-bg-section;
  margin: auto;
  background-position: center bottom 40px;
  background-repeat: no-repeat;
  background-size: 100%;
  background-image: url(../images/about/img-mob-milk@1x.png);
  @media screen and (min-width: 768px) {
    background-image: none;
  }
  @media screen and (min-width: 1280px) {
    display: block;
    background-position: center bottom -50px;
    background-image: url(../images/about/img-desktop-milk@1x.png);
  }
  @media screen and (min-device-pixel-ratio: 2),
    screen and (-webkit-min-device-pixel-ratio: 2),
    screen and (-o-min-device-pixel-ratio: 2/1),
    screen and (min-resolution: 192dpi),
    screen and (min-resolution: 2dppx) {
    background-image: url(../images/about/img-mob-milk@2x.png);
    @media screen and (min-width: 1280px) {
      background-image: url(../images/about/img-desktop-milk@2x.png);
    }
  }
}

.title-container {
  display: block;
  margin: 0 auto 26px;

  @media screen and (min-width: 768px) {
    margin: 0 auto 50px;
  }

  @media screen and (min-width: 1280px) {
    margin: 0 auto 55px;
  }
}

.about-label {
  font-family: $font-decor;
  font-weight: normal;
  font-size: 18px;
  line-height: 1.67;
  text-align: center;
  text-transform: uppercase;
  color: $title-text-color;
  text-decoration: none;
  margin: 0 auto 15px;

  @media screen and (min-width: 768px) {
    font-size: 26px;
    line-height: 1.15;
  }

  @media screen and (min-width: 1280px) {
    font-size: 30px;
    line-height: 1.13;
  }
}

.about-title {
  font-family: $font-decor;
  font-weight: normal;
  font-size: 34px;
  line-height: 1.15;
  text-align: center;
  margin: auto;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $accent-color;
  text-decoration: none;

  @media screen and (min-width: 768px) {
    font-size: 48px;
    line-height: 1.15;
  }

  @media screen and (min-width: 1280px) {
    font-size: 58px;
    line-height: 1.14;

    letter-spacing: 0.06em;
  }
}

.about-content {
  display: block;

  @media screen and (min-width: 768px) {
    display: flex;
  }
}

.cow {
  margin: auto;

  @media screen and (min-width: 768px) {
    margin-right: 17px;
  }

  @media screen and (min-width: 1280px) {
    margin-right: 74px;
  }

  // & img {
  //   width: 280px;

  //   @media screen and (min-width: 768px) {
  //     & {
  //       width: 340px;
  //     }
  //   }

  //   @media screen and (min-width: 1280px) {
  //     & {
  //       width: 516px;
  //     }
  //   }
  // }
}

img {
  display: block;
  margin: auto;
}

.text-container {
  max-width: 420px;
  padding: 0 30px;
  margin: auto;

  @media screen and (min-width: 768px) {
    padding: 50px 0 0;
    width: 340px;
    margin: 0;
  }
  @media screen and (min-width: 1280px) {
    max-width: max-content;
    width: 460px;
    padding: 51px 0 0;
  }
}

.about-text {
  display: block;
  margin-bottom: 15px;

  @media screen and (min-width: 768px) {
    margin-bottom: 62px;
  }
  @media screen and (min-width: 1280px) {
    margin-bottom: 45px;
  }
}

.text-primary {
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.62;
  margin-bottom: 30px;

  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: 1.76;
    letter-spacing: 0.04em;
  }
  @media screen and (min-width: 1280px) {
    margin-bottom: 35px;
    font-size: 16px;
    line-height: 1.76;
  }
}

.text-secondary {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.62;
  color: $secondary-text-color;

  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: 1.76;
    letter-spacing: 0.04em;
  }

  &__first {
    margin-bottom: 19px;

    @media screen and (min-width: 768px) {
      margin-bottom: 25px;
    }
    @media screen and (min-width: 1280px) {
      margin-bottom: 27px;
    }
  }
}

.about-button {
  display: flex;
  width: 176px;
  height: 44px;
  font-weight: bold;
  font-size: 14px;
  line-height: 1.29;

  padding: 0;

  align-items: center;
  text-align: center;
  justify-content: center;

  color: $accent-color;
  background: $color-text;
  border-radius: 22px;
  border: none;
  cursor: pointer;

  transition: background $cubicBezierMain $transitionTime;

  & span {
    margin-right: 10px;
  }

  &:focus,
  &:hover {
    background: $color-bg-btn_hover;
  }
}
